<template>
  <div class="v-container">
    <!-- 面包屑 -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ name: 'setting' }">设置</el-breadcrumb-item>
      <el-breadcrumb-item>工具管理</el-breadcrumb-item>
    </el-breadcrumb>
    <hr>
    <!-- 列表栏 -->
    <list></list>
    <!-- 默认隐藏的详情页【对话框形式】-->
    <info></info>
  </div>
</template>

<script>
  import list from './toolManage/list.vue'
  import info from './toolManage/info.vue'
  export default {
    name: 'toolSetting',
    components: {list,info}
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



