<template>
    <el-dialog title="工具信息"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name,'pageDataRef')"
    >
        <el-form :inline="true" :model="pageData" ref="pageDataRef" :rules="pageDataRules" label-width="130px" size="small">
            <el-form-item label="工具所属项目" prop="tool_project_id">
                <el-select v-model="pageData.tool_project_id" placeholder="请选工具所属项目" clearable>
                    <el-option v-for="item in tool_project_info_list"
                               :key="item.project_id"
                               :label="item.project_name"
                               :value="item._id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="工具上下文" prop="toolContext">
                <el-input v-model="pageData.toolContext" placeholder="设置工具上下文"></el-input>
            </el-form-item>
            <el-form-item label="工具类型" prop="tool_type_code_id" label-width="130px">
                <el-select v-model="pageData.tool_type_code_id" placeholder="请选工具类型" clearable>
                    <el-option v-for="item in tool_type_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="工具名称" prop="toolName">
                <el-input v-model="pageData.toolName"></el-input>
            </el-form-item>
            <el-form-item label="工具IP" prop="toolHost">
                <el-input v-model="pageData.toolHost"></el-input>
            </el-form-item>
            <el-form-item label="工具端口" prop="toolPort">
                <el-input v-model="pageData.toolPort"></el-input>
            </el-form-item>
            <el-form-item label="工具使用者" prop="toolUser">
                <el-input v-model="pageData.toolUser"></el-input>
            </el-form-item>
            <el-form-item label="工具使用者密码" prop="toolPassword">
                <el-input v-model="pageData.toolPassword"></el-input>
            </el-form-item>
            <el-form-item label="工具根路径" prop="toolRootPath">
                <el-input v-model="pageData.toolRootPath" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="备注" prop="remark">
                <el-input autosize type="textarea" v-model="pageData.remark" style="width:660px"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose(module_name,'pageDataRef')">关 闭</el-button>
            <el-button v-if="pageInfo.pageModel == 'add'" type="primary" @click="addToolInfo">确 定</el-button>
            <el-button v-else-if="pageInfo.pageModel == 'edit'" type="primary" @click="onUpdate">更 新</el-button>
        </div>
    </el-dialog>
</template>

<script>
    export default {
        name: 'toolManageInfo',
        data() {
            return {
                module_name:'toolManage',
                refresh_page_method: 'refresh_' + this.module_name,
                tool_type_code_list: null,
                tool_project_info_list: null,
                pageData: {
                    tool_project_id: '',
                    tool_type_code_id: '',
                    toolCode: '',
                    toolCodeId: '',
                    toolName: '',
                    toolPort: 0,
                    toolHost: '',
                    toolUser: '',
                    toolPassword: '',
                    toolContext: '',
                    remark: '',
                },
                pageDataRules: {
                    toolCodeId: [{required: true, message: '请选择工具类型', trigger: 'blur'}],
                    toolName: [{required: true, message: '请输入工具名称', trigger: 'blur'}],
                    // toolHost: [{required: true, message: '请输入工具IP', trigger: 'blur'}, {validator: this.$checkIP, trigger: 'blur'}],
                    toolHost: [{required: true, message: '请输入工具IP', trigger: 'blur'}],
                    toolUser: [{required: true, message: '请输入工具登录用户', trigger: 'blur'},],
                    toolPort: [{validator: this.$checkPort, trigger: 'blur'}]
                }
            }
        },

        computed: {
            pageInfo() {
                return this.$setPageInfo(this.module_name)
            }
        },

        mounted() {
            this.set_tool_type();
            this.set_tool_project();
        },
        methods: {
            async set_tool_type() /*设置工具类型*/ {
                if (!this.tool_type_code_list) {
                    this.tool_type_code_list = await this.$getCodeValuesByCode('tool_type');
                }
            },
            async set_tool_project() /*设置项目信息*/ {
                if (!this.tool_project_info_list) {
                    this.tool_project_info_list = await this.$getProjectCodeInfo();
                }
            },
            async r_addToolInfo(insertData) // 新增工具信息
            {
                let R = new this.$http();
                let {data} = await R.post(insertData, 'OAMP_insert_tool_info');
                if (data != null && data !== 'null')
                    return data.data;
                else
                    return null;
            },
            addToolInfo() // 新增工具信息
            {
                let _this = this;
                this.$refs['pageDataRef'].validate(async (valid) => {
                        if (valid) {
                            let insertData = _this.pageData;
                            let res = await _this.r_addToolInfo(insertData);
                            if (res != null) {
                                _this.$message.success('新增工具信息成功');
                                _this.closeDialog();
                            } else {
                                _this.$message.error('新增工具信息失败');
                            }
                        }
                    }
                )
            },
            async onUpdate() // 修改工具信息
            {

                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.$update(item, 'OAMP_update_tool_info');
                        if (res != null) {
                            _this.$message.success('修改信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('修改信息失败');
                        }
                    }
                });
            },
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }
</style>



