<template>
    <div style="padding-top: 2px;">
        <el-form  :inline="true" :model="searchFormData" ref="searchFormRef" :rules="searchFormRules"  size="mini">
            <el-form-item label="工具类型" prop="tool_type_code_id">
                <el-select v-model="searchFormData.tool_type_code_id" placeholder="工具类型" clearable>
                    <el-option v-for="item in tool_type_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="工具名称" prop="toolName">
                <el-input v-model="searchFormData.toolName" placeholder="工具名称" ></el-input>
            </el-form-item>
            <el-form-item label="工具端口" prop="toolPort">
                <el-input v-model="searchFormData.toolPort" placeholder="工具端口"></el-input>
            </el-form-item>
            <el-form-item label="工具IP" prop="toolHost">
                <el-input v-model="searchFormData.toolHost" placeholder="工具IP"></el-input>
            </el-form-item>
            <el-form-item label="工具使用者" prop="toolUser">
                <el-input v-model="searchFormData.toolUser" placeholder="工具使用者"></el-input>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[14, 20, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper"
                    >
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onAdd(module_name)"><i class="iconfont icon-xinzeng"></i> 新增</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)" >
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column fixed prop="tool_type_code_id" :formatter="format_tool_type" label="工具类型" width="180"></el-table-column>
            <el-table-column fixed prop="tool_project_id"  :formatter="format_project_name" label="所属项目名称"  :show-overflow-tooltip="true" width="180"></el-table-column>
            <el-table-column prop="toolName" label="工具名称" :show-overflow-tooltip="true"  width="180"></el-table-column>
            <el-table-column prop="toolPort" label="工具端口" width="120"></el-table-column>
            <el-table-column prop="toolHost" label="工具IP" width="180"></el-table-column>
            <el-table-column prop="toolUser" label="工具使用者" width="180"></el-table-column>
            <el-table-column prop="toolContext" label="工具所属项目" :show-overflow-tooltip="true" width="180"></el-table-column>
            <el-table-column prop="createTime" label="创建时间"></el-table-column>
            <el-table-column prop="updateTime" label="修改时间"></el-table-column>
            <el-table-column fixed="right" label="操作" width="120">
                <template slot-scope="scope">
                    <el-row :gutter="10">
                        <el-col :span="8">
                            <el-tooltip class="item" effect="dark" content="查看" placement="left">
                                <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-chakan"></i></el-button>
                            </el-tooltip>
                        </el-col>
                        <el-col :span="8">
                            <el-tooltip class="item" effect="dark" content="修改" placement="left">
                                <el-button @click.native.prevent="onEdit(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-xiugai"></i></el-button>
                            </el-tooltip>
                        </el-col>
                        <el-col :span="8">
                            <el-tooltip class="item" effect="dark" content="删除" placement="left">
                                <el-button @click.native.prevent="onDelete(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-shanchu icon-red"></i></el-button>
                            </el-tooltip>
                        </el-col>
                    </el-row>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'toolManageList',
        data() {
            return {
                module_name:'toolManage',
                refresh_page_method: 'refresh_' + this.module_name,
                tool_type_code_list: null,
                tool_project_info_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 14,
                    pageCount: 1,
                    order: '+project'
                },
                tableData: [],
                searchFormRules: {
                    toolHost: [{validator: this.$checkIP, trigger: 'blur'}],
                    toolPort: [{validator: this.$checkPort, trigger: 'blur'}]
                }
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        mounted() {
            this.set_tool_type();
            this.set_tool_project();
            this.onSearch();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.onSearch();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.onSearch();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.onSearch();
            },
            async set_tool_type() /*设置工具类型*/
            {
                if (!this.tool_type_code_list) {
                    this.tool_type_code_list = await this.$getCodeValuesByCode('tool_type');
                }
            },
            async set_tool_project() /*设置项目信息*/
            {
                if (!this.tool_project_info_list) {
                    this.tool_project_info_list = await this.$getProjectCodeInfo();
                }
            },
            format_tool_type(row, column, cellValue) /*格式化项目类型信息*/
            {
                if (this.tool_type_code_list && this.tool_type_code_list.length > 0) {
                    let res = this.tool_type_code_list.filter(item => item.codeId === row.tool_type_code_id);
                    if (res != null && res.length > 0) {
                        return res[0].codeValue;
                    }
                }
            },
            format_project_name(row, column, cellValue) /*格式化项目类型信息*/
            {
                if (this.tool_project_info_list && this.tool_project_info_list.length > 0) {
                    let res = this.tool_project_info_list.filter(item => item._id === row.tool_project_id);
                    if (res != null && res.length > 0) {
                        return res[0].project_name;
                    }
                }
            },
            async r_searchTable(searchData) // 查询列表
            {
                let R = new this.$http();
                let {data} = await R.post(searchData, 'OAMP_search_tool_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data
            },
            async r_delete_tool_info(rowData) // 删除列表信息
            {
                let R = new this.$http();
                let {data} = await R.post(rowData, 'OAMP_delete_tool_info');
                return data != null && data !== 'null';
            },
            onSearch() // 查询按钮
            {
                let searchData = this.searchFormData;
                this.r_searchTable(searchData)
            },
            onDelete(index, rows)  // 删除
            {
                let rowData = rows[index];
                if (this.r_delete_tool_info(rowData)) {
                    rows.splice(index, 1);
                    this.$message.success('删除数据成功');
                    this.onSearch();
                }
                else {
                    this.$message.error('删除数据失败');
                }
            },
        }

    }
</script>
<style scoped>
</style>


